require('./bootstrap');

// Exibir o Snackbar com a mensagem de erro
function showSnackbar(message) {
    console.log('Snackbar message:', message);
    const snackbar = document.getElementById('snackbar');
    const snackbarMessage = document.getElementById('snackbar-message');

    // Defina o texto da mensagem do snackbar
    snackbarMessage.textContent = message;

    // Exibe o snackbar
    snackbar.classList.remove('d-none'); // Remover a classe d-none para mostrar

    // Esconde aps 3 segundos
    setTimeout(function() {
        snackbar.classList.add('d-none'); // Volta a ocultar aps 3 segundos
    }, 3000);
}
